// src/app/components/HeroCarousel/HeroCarousel.tsx
'use client';

import React, { useState, useEffect, useRef } from 'react';
import styles from './HeroCarousel.module.css';

const HeroCarousel = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [progress, setProgress] = useState(0);
  const [isPlaying, setIsPlaying] = useState(true);
  const progressInterval = useRef<NodeJS.Timeout | null>(null);
  const videoRefs = useRef<(HTMLVideoElement | null)[]>([]);
  const fgVideoRefs = useRef<(HTMLVideoElement | null)[]>([]);

  const carouselItems = [
    {
      bgVideo: '/videos/hero-carousel/1_Creative_Alchemy_Desktop.mp4',
      fgVideo: '/videos/hero-carousel/1_Creative_Alchemy_Secondary.mp4',
      title: 'A precious new hybrid',
      nextTitle: 'Venice film festival',
    },
    {
      bgVideo: '/videos/hero-carousel/2_Trinity_Desktop.mp4',
      fgVideo: '/videos/hero-carousel/2_Trinity-secondary-test.mp4',
      title: '3 is the magic number',
      nextTitle: 'Creative Alchemy',
    },
    {
      bgVideo: '/videos/hero-carousel/3_Precious_Hybrid_Desktop.mp4',
      fgVideo: '/videos/hero-carousel/3_Precious_Hybrid_Secondary-test.mp4',
      title: 'Shake up the senses',
      nextTitle: 'Time is an illusion',
    },
    {
      bgVideo: '/videos/hero-carousel/4_Venice_Desktop.mp4',
      fgVideo: '/videos/hero-carousel/4_Venice_Secondary-test.mp4',
      title: 'Appear and disappear',
      nextTitle: 'The power of transformation',
    },
    {
      bgVideo: '/videos/hero-carousel/5_TimeIllusion_Desktop.mp4',
      fgVideo: '/videos/hero-carousel/5_TimeIllusion-secondary-test.mp4',
      title: 'Create the unexpected',
      nextTitle: 'The 365 rendez-vous',
    },
  ];

  const startProgress = () => {
    if (progressInterval.current) clearInterval(progressInterval.current);
    setProgress(0);

    progressInterval.current = setInterval(() => {
      setProgress((prev) => {
        if (prev >= 100) {
          handleNext();
          return 0;
        }
        return prev + 0.5;
      });
    }, 100);
  };

  const handleNext = () => {
    setActiveIndex((prev) => (prev + 1) % carouselItems.length);
    setProgress(0);
  };

  const handlePrev = () => {
    setActiveIndex((prev) => (prev - 1 + carouselItems.length) % carouselItems.length);
    setProgress(0);
  };

  useEffect(() => {
    if (isPlaying) {
      startProgress();
    } else if (progressInterval.current) {
      clearInterval(progressInterval.current);
    }

    return () => {
      if (progressInterval.current) {
        clearInterval(progressInterval.current);
      }
    };
  }, [isPlaying, activeIndex]);

  useEffect(() => {
    const safePlay = (video: HTMLVideoElement | null) => {
      if (!video) return;
      try {
        video.currentTime = 0;
        video.pause();
        video.play();
      } catch (err) {
        console.warn('Autoplay failed:', err);
      }
    };

    videoRefs.current.forEach((v) => {
      if (v) {
        v.currentTime = 0;
        v.pause();
      }
    });

    fgVideoRefs.current.forEach((v) => {
      if (v) {
        v.currentTime = 0;
        v.pause();
      }
    });

    safePlay(videoRefs.current[activeIndex]);
    safePlay(fgVideoRefs.current[activeIndex]);

    const prevIndex = (activeIndex - 1 + carouselItems.length) % carouselItems.length;
    safePlay(videoRefs.current[prevIndex]);
    safePlay(fgVideoRefs.current[prevIndex]);
  }, [activeIndex]);

  return (
    <section className={styles.heroCarousel}>
      <div className={styles.carouselWrapper}>
        {/* Background Carousel */}
        <div className={styles.bgCarousel}>
          <div className={styles.videos}>
            {carouselItems.map((item, index) => (
              <div
                key={`bg-${index}`}
                className={`
                  ${styles.videoMask}
                  ${index === activeIndex ? styles.active : ''}
                  ${index === (activeIndex - 1 + carouselItems.length) % carouselItems.length ? styles.prev : ''}
                `}
                style={{
                  transform: `
                    ${index === activeIndex ? 'scale(1.09328) skewX(4deg)' : ''}
                    ${index < activeIndex ? 'translateX(-110%) scale(1.09328) skewX(4deg)' : ''}
                    ${index > activeIndex ? 'translateX(110%) scale(1.09328) skewX(4deg)' : ''}
                  `,
                }}
              >
                <div
                  className={`${styles.videoMask} ${styles.cover}`}
                  style={{
                    transform: `
                      ${index === activeIndex ? 'scale(1.09328) skewX(-4deg)' : ''}
                      ${index < activeIndex ? 'translateX(110%) scale(1.09328) skewX(-4deg)' : ''}
                      ${index > activeIndex ? 'translateX(-110%) scale(1.09328) skewX(-4deg)' : ''}
                    `,
                  }}
                >
                  <video
                    ref={(el) => {
                      videoRefs.current[index] = el;
                    }}
                    className={styles.video}
                    src={item.bgVideo}
                    playsInline
                    loop
                    muted
                  />
                </div>
              </div>
            ))}
          </div>
        </div>

        {/* Foreground Carousel */}
        <div className={styles.fg}>
          <div className={styles.fgCarousel}>
            <div className={styles.videos}>
              {carouselItems.map((item, index) => (
                <div
                  key={`fg-${index}`}
                  className={`
                    ${styles.videoMask}
                    ${index === activeIndex ? styles.active : ''}
                    ${index === (activeIndex - 1 + carouselItems.length) % carouselItems.length ? styles.prev : ''}
                  `}
                  style={{
                    transform: `
                      ${index === activeIndex ? 'scale(1.08975) skewX(4deg)' : ''}
                      ${index < activeIndex ? 'translateX(-110%) scale(1.08975) skewX(4deg)' : ''}
                      ${index > activeIndex ? 'translateX(110%) scale(1.08975) skewX(4deg)' : ''}
                    `,
                  }}
                >
                  <div
                    className={`${styles.videoMask} ${styles.cover}`}
                    style={{
                      transform: `
                        ${index === activeIndex ? 'scale(1.08975) skewX(-4deg)' : ''}
                        ${index < activeIndex ? 'translateX(110%) scale(1.08975) skewX(-4deg)' : ''}
                        ${index > activeIndex ? 'translateX(-110%) scale(1.08975) skewX(-4deg)' : ''}
                      `,
                    }}
                  >
                    <video
                      ref={(el) => {
                        fgVideoRefs.current[index] = el;
                      }}
                      className={styles.video}
                      src={item.fgVideo}
                      playsInline
                      loop
                      muted
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Text Carousel */}
          <div className={styles.textCarousel}>
            <div className={styles.textCarouselItem}>
              <div>
                <h1 className={styles.siteHeadline}>
                  {carouselItems[activeIndex].title.split(' ').map((word, i) => (
                    <React.Fragment key={i}>
                      {i > 0 && <span className={`${styles.char} ${styles.space}`}> </span>}
                      {word.split('').map((char, j) => (
                        <span
                          key={j}
                          className={styles.char}
                          style={{
                            opacity: 1,
                            filter: 'blur(0px) brightness(1)',
                            willChange: 'transform',
                          }}
                        >
                          {char}
                        </span>
                      ))}
                    </React.Fragment>
                  ))}
                </h1>
              </div>
              <div className={styles.next} style={{ opacity: 1, willChange: 'transform' }}>
                <p className={styles.caption}>Next up</p>
                <p className={styles.nextTitle}>
                  {carouselItems[(activeIndex + 1) % carouselItems.length].nextTitle}
                </p>
                <div
                  className={styles.progressBar}
                  style={{ '--v': 'visible', '--p': progress } as React.CSSProperties}
                />
              </div>
              <div className={styles.cta} style={{ opacity: 1, willChange: 'transform' }}>
                <p className={styles.caption}>Read article</p>
              </div>
            </div>
          </div>
        </div>

        {/* Navigation */}
        <nav className={styles.nav}>
          <button className={styles.prev} onClick={handlePrev}>
            <svg viewBox="0 0 4 5" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M0 2.5L3.75 4.66506L3.75 0.334936L0 2.5Z" fill="currentColor" />
            </svg>
          </button>
          <button className={styles.next} onClick={handleNext}>
            <svg viewBox="0 0 4 5" fill="none" xmlns="http://www.w3.org/2000/svg">
              <path d="M0 2.5L3.75 4.66506L3.75 0.334936L0 2.5Z" fill="currentColor" />
            </svg>
          </button>
        </nav>

        {/* Progress Indicators */}
        <div className={styles.progress}>
          {carouselItems.map((_, index) => (
            <div
              key={`progress-${index}`}
              className={styles.progressBar}
              style={{
                '--v': index === activeIndex ? 'visible' : 'hidden',
                '--p': index === activeIndex ? progress : 0,
              } as React.CSSProperties}
            />
          ))}
        </div>
      </div>

      {/* Cursor Elements */}
      <div className={styles.cursorOutline} style={{ opacity: 0 }}>
        <span aria-hidden="true" className={styles.caption}>
          Read article
        </span>
      </div>
      <div className={styles.cursorLabel} style={{ opacity: 0 }}>
        <span className={styles.caption}>Read article</span>
      </div>
    </section>
  );
};

export default HeroCarousel;
